import C from './constants';

//actions on data received from response json

export const addHotResult = (data) =>
({
  type : C.ADD_HOT_RESULT,
  wrapperType : data.wrapperType,
  artistId : data.artistId,
  collectionId : data.collectionId,
  trackId : data.trackId,
  artistName : data.artistName,
  collectionName : data.collectionName,
  trackName : data.trackName,
  artworkUrl : data.artworkUrl,
  artistViewUrl : data.artistViewUrl,
  collectionViewUrl : data.collectionViewUrl,
  feedUrl : data.feedUrl,
  releaseDate : data.releaseDate,
  country : data.country,
  genres : data.genres
})

export const removeHotResult = (collectionId,trackId) =>
({
   type : C.REMOVE_HOT_RESULT,
   collectionId,
   trackId
})

export const updateHotResults = (newData) =>
({
  type : C.UPDATE_HOT_RESULTS,
  newData
})

export const initializeHotResults = () =>
({
  type : C.INITIALIZE_HOT_RESULTS
})

// actions on items added as flattred

export const addFlattredItem = (data) =>
({
  type : C.ADD_FLATTRED,
  wrapperType : data.wrapperType,
  artistId : data.artistId,
  collectionId : data.collectionId,
  trackId : data.trackId,
  artistName : data.artistName,
  collectionName : data.collectionName,
  trackName : data.trackName,
  artworkUrl : data.artworkUrl,
  artistViewUrl : data.artistViewUrl,
  collectionViewUrl : data.collectionViewUrl,
  feedUrl : data.feedUrl,
  releaseDate : data.releaseDate,
  country : data.country,
  genres : data.genres
})

export const removeFlattredItem = (collectionId,trackId) =>
({
   type : C.REMOVE_FLATTRED,
   collectionId,
   trackId
})

export const updateFlattredItems = (newData) =>
({
  type : C.UPDATE_FLATTRED,
  newData
})

export const updateLastEpisode = (collectionId,lastEpisode) =>
({
  type : C.UPDATE_LAST_EPISODE,
  collectionId,
  lastEpisode
})

export const updateSubscribed = (collectionId,subscribed) =>
({
  type : C.UPDATE_SUBSCRIBED,
  collectionId,
  subscribed
})

export const initializeFlattredList = () =>
({
  type : C.INITIALIZE_FLATTRED
})

// actions on connection token

export const updateToken = (value) =>
({
  type : C.UPDATE_TOKEN,
  value
})

// actions on connected user data

export const updateUser = (id,idv3,username,email,firstname,lastname,country,link,avatar) =>
({
  type : C.UPDATE_USER,
  id,
  idv3,
  username,
  email,
  firstname,
  lastname,
  country,
  link,
  avatar
})

export const initializeUser = () =>
({
  type : C.INITIALIZE_USER
})



