import React from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';
import PodcastButtons from './podcastButtons.jsx';
import Loader from './loader.jsx';
import {StyledPodcastItem,PodcastInfosList,ArtworkImg,MyScrollBar} from '../../styledComponents/styledComponents.jsx';
import {addFlattredItem,removeFlattredItem,removeHotResult} from '../../redux/actions.js';
import '../../common/css/scrollbar.css';


export default class PodcastItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading : false
    }
    this.onClick = this.onClick.bind(this);
  }


  onClick(e,action){
    const {item} = this.props;
    const {store} = this.context;

    e.preventDefault();

    if(action === "remove"){
        alert('TODO');
    }

    if(action === "mark"){
        const {user} = this.context.store.getState();

        this.setState({loading : true});
        fetch('https://flattrback.herokuapp.com/firebase/add' ,{method : "post",
              body : JSON.stringify({userId : user.id,markedItem : item}),
              headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
              }})
                 .then(res =>{  console.log(res); return  res.json(); })
                 .then(data => {
                   if(data.success){
                       store.dispatch(addFlattredItem(item));
                       //store.dispatch(removeHotResult(item.collectionId,item.trackId));
                       alert('item marked');
                   }
                   else {
                     alert(data.message);
                   }

                   this.setState({loading : false});

                 })
                 .catch(err => {
                       this.setState({loading : false});
                       alert("a server error occured. Try again");
                 });



    }
  }


  render() {
    const {item} = this.props;
    const {loading} = this.state;

    return (
         <div className={`scrollbar`} id="styleScrollbar">
          <StyledPodcastItem>
              <div className="artWork">
                <ArtworkImg src={item.artworkUrl} alt="" />
                { (loading)? <Loader loaded={false} size="small" /> :
                  <PodcastButtons item={item}  podcastItemAction={this.onClick}/>
                }
              </div>
              <PodcastInfosList type={"more"}>
                  <li><span>Collection id:</span> {item.collectionId}</li>
                  <li><span>Collection name:</span> {item.collectionName}</li>
                  <li><span>Artist id: </span> {item.artistId}</li>
                  <li><span>Artiste name:</span> {item.artistName}</li>
                  <li><span>Track id:</span> {item.trackId}</li>
                  <li><span>Track name:</span> {item.trackName}</li>
                  <li><span>Country:</span> {item.country}</li>
                  <li><span>Genres:</span> {item.genres}</li>
                  <li><span>Release date:</span> {item.releaseDate}</li>
                  <li><span>Wrapper type:</span> {item.wrapperType}</li>
              </PodcastInfosList>
          </StyledPodcastItem>
         </div>);
  }
}

PodcastItem.propTypes = {
};

PodcastItem.contextTypes = {
  store : PropTypes.object
}



