import React from 'react';
import PropTypes from 'prop-types';
import Modal from 'react-modal';
import $ from 'jquery';
import Loader from './loader.jsx';
import MarkedPodcastItemMore from './markedPodcastItemMore.jsx';
import MarkedPodcastButtons from './markedPodcastButtons.jsx';
import {StyledMarkedPodcastItem,MarkedPodcastInfosList,ArtworkImg,MoreButton,ModalStyle} from '../../styledComponents/styledComponents.jsx'
import {updateSubscribed,updateLastEpisode,removeFlattredItem} from '../../redux/actions.js';
import '../../common/css/listScrollbar.css';

export default class MarkedPodcastItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading : false,
      isOpen : false
    }

    this.onSubscribeClick = this.onSubscribeClick.bind(this);
    this.onUnmarkClick = this.onUnmarkClick.bind(this);
    this.onMoreClick = this.onMoreClick.bind(this);
    this.onCloseModalClick = this.onCloseModalClick.bind(this);
  }

  onUnmarkClick(e){
    const {store} = this.context;
    const {user} = this.context.store.getState();
    const {item} = this.props;

    e.preventDefault();

    this.setState({loading : true});
    fetch('https://flattrback.herokuapp.com/firebase/delete' ,{method : "post",
          body : JSON.stringify({userId : user.id,itemId : item.collectionId}),
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
               if(data.success){
                 store.dispatch(removeFlattredItem(item.collectionId,item.trackId));
                 alert('unmark successfully');
                 $(document).trigger('flattredRefreshed');
                 this.setState({loading : false});
               }
             })
             .catch(err => {
                   this.setState({loading : false});
                   alert("server error has occured. try again");
             });



  }

  onSubscribeClick(e,subscribed){
    const {store} = this.context;
    const {user} = this.context.store.getState();
    const {item} = this.props;

    e.preventDefault();

    if(!subscribed){
      if(item.feedUrl){
      const payload = {url : item.feedUrl };
      this.setState({loading : true});
      fetch('https://flattrback.herokuapp.com/feedrss/lastEpisode' ,{method : "post",
            body : JSON.stringify(payload),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }})
               .then(res =>{  console.log(res); return  res.json(); })
               .then(data => {
                       if(data.success){
                          var newItem = item;
                          newItem.subscribed = true;
                          newItem.lastEpisode = data.article;

                          alert(JSON.stringify(newItem));

                          fetch('https://flattrback.herokuapp.com/firebase/removeAndAdd' ,{method : "post",
                                body : JSON.stringify({userId : user.id,markedItem : newItem}),
                                headers: {
                                    'Accept': 'application/json',
                                    'Content-Type': 'application/json'
                                }})
                                   .then(res =>{  console.log(res); return  res.json(); })
                                   .then(data => {
                                     if(data.success){
                                       store.dispatch(updateSubscribed(item.collectionId,true));
                                       store.dispatch(updateLastEpisode(item.collectionId,data.article));
                                       //this.setState({subscribeButtonText : 'disable'});
                                       //$(document).trigger('itemSubscribed');
                                       alert('subscribed');

                                     }
                                     this.setState({loading : false});
                                   })
                                   .catch(err => {
                                        this.setState({loading : false});
                                        alert("server error has occured. try again");
                                   });

                       }
                       else{
                          this.setState({loading : false});
                          alert(JSON.stringify(data.message));
                       }


               })
               .catch(err => {
                    this.setState({loading : false});
                    alert("server error has occured. try again");
               });

          }

          else {
            alert('this item doesnt have an feed rss url');
          }

    }

    if(subscribed){
        var newItem = item;
        newItem.subscribed = false;
        newItem.lastEpisode = {}

        this.setState({loading : true});
        fetch('https://flattrback.herokuapp.com/firebase/removeAndAdd' ,{method : "post",
              body : JSON.stringify({userId : user.id,markedItem : newItem}),
              headers: {
                  'Accept': 'application/json',
                  'Content-Type': 'application/json'
              }})
                 .then(res =>{  console.log(res); return  res.json(); })
                 .then(data => {
                   if(data.success){
                     store.dispatch(updateSubscribed(item.collectionId,false));
                     store.dispatch(updateLastEpisode(item.collectionId,{}));
                     //this.setState({subscribeButtonText : 'subscribe'});
                     //$(document).trigger('itemUnsubscribed');
                     alert('disabled');
                   }
                   this.setState({loading : false});
                 })
                 .catch(err => {
                         this.setState({loading : false});
                         alert("a server error has occured. Try again please");
                 });
    }
  }

  onMoreClick(e){
    e.preventDefault();
    this.setState({isOpen : true});
  }

  onCloseModalClick(e){
    e.preventDefault();
    this.setState({isOpen : false});
  }

  render() {
    const {isOpen,loading} = this.state;
    const {item} = this.props;

    return (
      <StyledMarkedPodcastItem>
        <div className="artWork">
          <ArtworkImg src={item.artworkUrl} alt="" />
          {
            (loading)? <Loader loaded={false} size="small"/> :
                <MarkedPodcastButtons podcastItemSubscribed={this.onSubscribeClick}
                                      podcastItemActionUnmark={this.onUnmarkClick}
                                      subscribed={item.subscribed}/>
          }
        </div>
        <div>
          <div className={`listScrollbar`} id="styleListScrollbar">
            <MarkedPodcastInfosList type={"more"}>
                <li><span>Collection name:</span> {item.collectionName}</li>
                <li><span>Artiste name:</span> {item.artistName}</li>
                <li><span>Country:</span> {item.country}</li>
                <li><span>Release date:</span> {item.releaseDate}</li>
            </MarkedPodcastInfosList>
          </div>
          { (item.subscribed)?
            <MoreButton onClick={this.onMoreClick}>More...</MoreButton> : ""
          }
        </div>
              <Modal
                isOpen={isOpen}
                style={ModalStyle}
                closeTimeoutMS={50}>
                  <MarkedPodcastItemMore item={item} onClick={this.onCloseModalClick} />
              </Modal>
      </StyledMarkedPodcastItem>);
  }
}

MarkedPodcastItem.propTypes = {
};

MarkedPodcastItem.contextTypes = {
  store : PropTypes.object
}



