import React from 'react';
import PropTypes from 'prop-types';
import AppTemplate from '../appTemplate.jsx';
import Loader from '../dashboard/loader.jsx';
import {Redirect} from 'react-router-dom';
import {updateUser,updateToken} from '../../redux/actions.js';


export default class ValidateAuthentication extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        redirectTo : null
    }
  }

  componentWillMount() {
    const {match,location} = this.props ;
    const {store} = this.context;

    const code = location.search.split("=")[1];
    //alert(code);

    fetch('https://flattrback.herokuapp.com/getToken/' +code   ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(res => {
                 console.log(JSON.stringify(res));
                 if(res.success){
                   const user = res.data;
                   const token = res.token.access_token;
                   store.dispatch(
                     updateUser(
                       user.id,user.idv3,user.username,user.email,user.firstname,
                       user.lastname,user.country,user.link,user.avatar));
                   store.dispatch(updateToken(token));
                   this.setState({redirectTo: "/dashboard/pods"});
                 }
                 else{
                   this.setState({redirectTo: "/signIn"});
                 }

             })
             .catch(err => {
                    this.setState({redirectTo: "/signIn"});
             });

  }

  render() {
    const {redirectTo} = this.state;
    return (
      <AppTemplate title="validate">
        {
          (!redirectTo)?<div style={{marginTop:300}}><Loader loaded={false} /></div> :
              <Redirect to={redirectTo} />

        }
      </AppTemplate>
    );
  }
}

ValidateAuthentication.contextTypes = {
  store : PropTypes.object
}

ValidateAuthentication.propTypes = {
};



