import React, {PropTypes} from 'react';
import {Redirect} from 'react-router-dom';
import AppTemplate from '../appTemplate.jsx';
import {SignInButton,SigninBody} from '../../styledComponents/styledComponents.jsx';


export default class SignIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loginUrl : null
    }
    this.onClick = this.onClick.bind(this);
  }

  componentWillMount() {
    fetch('https://flattrback.herokuapp.com/authenticate'  ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(res => {
                    console.log(JSON.stringify(res));
                    this.setState({loginUrl:res.redirectUrl});
             })
             .catch(err => {
                        alert('a server error has occured. Load again please');
                        //alert(JSON.stringify(err));
             });
  }

  onClick(e){
    //e.preventDefault();

  }


  render() {
    const {loginUrl} = this.state;
    return (
      <AppTemplate title="signIn">
        <SigninBody >
           <SignInButton href={loginUrl} onClick={this.onClick}>
             Sign in with your flattr account
           </SignInButton>
        </SigninBody>
      </AppTemplate>
    );
  }
}

SignIn.propTypes = {
};



