import React from 'react';
import PropTypes from 'prop-types';
import {HashRouter,Route,Switch,Redirect} from 'react-router-dom';
import SignIn from './components/authentication/signin.jsx';
import ValidateAuthentication from './components/authentication/validateAuthentication.jsx'
import PodDashboard from './components/dashboard/podcastsDashboard.jsx';
import FlattredDashboard from './components/dashboard/flattredDashboard.jsx';
import AccountDashboard from './components/dashboard/accountDashboard.jsx';
import Whoops404 from './components/whoops404.jsx';
import './common/css/style.css';

import storeFactory from './redux/store.js';

const store = storeFactory();


export default class App extends React.Component {

  getChildContext() {
    return {
      store : store
    }
  }

  componentWillMount() {

    this.unsubscribe = store.subscribe(() => {
            this.forceUpdate();
            localStorage['redux-store'] = JSON.stringify(store.getState());
          });
  }

  componentWillUnmount() {
    this.unsubscribe();
  }

  render() {
    return (
      <div className="app">
        <HashRouter>
          <Switch>
            <Route exact path="/signIn" component={SignIn} />
            <Route path ="/validate" component={ValidateAuthentication}/>
            <Route path="/dashboard/pods" component={PodDashboard} />
            <Route path="/dashboard/flattred" component={FlattredDashboard} />
            <Route path="/dashboard/account" component={AccountDashboard} />
            <Route component={Whoops404} />
          </Switch>
        </HashRouter>
      </div>);
  }
}

App.propTypes = {
};

App.childContextTypes = {
  store : PropTypes.object.isRequired
}



