import React,{Component} from 'react';
import PropTypes from 'prop-types';
import Avatar from 'react-avatar';
import {Redirect} from 'react-router-dom';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import IconMenu from 'material-ui/IconMenu';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';
import NavigationExpandMoreIcon from 'material-ui/svg-icons/navigation/expand-more';
import {Toolbar, ToolbarGroup, ToolbarSeparator, ToolbarTitle} from 'material-ui/Toolbar';
import {StyledAppBar,BarLeftBox,Introduction,BarRightBox,StyledBarIcon, StyledLink} from '../styledComponents/styledComponents.jsx';
import AppMenu from './appMenu.jsx';
import {initializeUser,updateToken} from '../redux/actions.js';
import AppBarBackground from './appbarbackground';


class AppBar extends Component {
  constructor(props){
    super(props);
    this.state = {
       redirectTo : null
    }

    this.onClick = this.onClick.bind(this);
  }

  onClick(e,item){
    const {store} = this.context;
    //alert('click');
    store.dispatch(initializeUser());
    store.dispatch(updateToken(null));
    this.setState({redirectTo : '/signIn'})
  }

  render(){
  const {title} = this.props;
  const {redirectTo} = this.state;
  const {user} = this.context.store.getState();
  return(
    (redirectTo)?
    <Redirect to={redirectTo} /> :
    <StyledAppBar >
        <AppBarBackground />
        <BarLeftBox>
          <div>
            <StyledBarIcon src={require(`../common/images/logo.png`)} alt="" />
          </div>
          <div>
            {
              (title !== "signIn" && title !== "validate")?
                  <AppMenu  title={title} /> :
                  <Introduction>
                    Connect and start subscribing to your<br/>
                    favourite podcast shows
                  </Introduction>
            }

          </div>
        </BarLeftBox>
        {
          (title !== "signIn" && title !== "validate")?
            <BarRightBox>
              <div>
                <Avatar value="86%" name="cloud strife" src={user.avatar} round={true} size={50}
                  style={{width : 50 , height : 50}} color="#000" />
              </div>
              <div>
                <MuiThemeProvider>
                  <IconMenu iconButtonElement={
                    <IconButton touch={true}>
                       <NavigationExpandMoreIcon  />
                    </IconButton>
                  } >
                    <MenuItem>
                      <StyledLink href="#/dashboard/account" >Account</StyledLink>
                    </MenuItem>
                    <MenuItem primaryText="Logout" onClick={(e)=>this.onClick(e,'logout')}/>
                  </IconMenu>
                </MuiThemeProvider>
              </div>
            </BarRightBox> : ""
        }
  </StyledAppBar>
    )
  }
}

AppBar.contextTypes = {
  store : PropTypes.object
}

export default AppBar;



