import React from 'react';
import PropTypes from 'prop-types';
import Paginate from 'react-paginate';
import $ from 'jquery';
import {scroller,Element,scroll} from 'react-scroll';
import PodcastItems from './podcastItems.jsx';
import {StyledPodcastsPagination} from '../../styledComponents/styledComponents.jsx';

export default class PodcastsPagination extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
       pageData : [],
       pageCount : 0
    }

    this.handlePageClick = this.handlePageClick.bind(this);
    this.setPageData = this.setPageData.bind(this);
  }

  setPageData(selected,type){
    if(type === "hot"){
      const {hotResults} = this.context.store.getState();


      this.setState({pageData :
        hotResults.slice(selected*20,(selected+1)*20 ),
        pageCount : Math.ceil(hotResults.length / 20)})
    }

    if(type === "flattred"){
      const {flattredItems} = this.context.store.getState();

      this.setState({pageData :
        flattredItems.slice(selected*20,(selected+1)*20 ),
        pageCount : Math.ceil(flattredItems.length / 20) })
    }

    if(type === "reset"){
      this.setState({pageData: []});
    }
  }

  componentWillMount() {
    const {type} = this.props;
    this.setPageData(0,type);
  }

  componentDidMount() {

    $(document).on('newhotResults',(e)=>{
        this.setPageData(0,"hot");
    });

    $(document).on('flattredRefreshed',(e)=>{
      this.setPageData(0,"flattred");
    });

    $(document).on('flattredReset',(e)=> {
      this.setPageData(0,"reset");
    });
  }


  handlePageClick(data){
    const {type} = this.props;
    this.setPageData(data.selected,type);
    scroller.scrollTo("podcastsElement",{
      duration : 500,
      smooth : true
    });
  }

  render() {
    const {pageData,pageCount} = this.state;
    const {type} = this.props;

    return (
      <StyledPodcastsPagination>
        <Element name="podcastsElement" >
        <PodcastItems pageData={pageData} type={type} className="items" />
        </Element>
        {
          (pageCount >1)?
              <Paginate previousLabel={"<"}
                      nextLabel={">"}
                      breakLabel={<a href="">...</a>}
                      breakClassName={"break-me"}
                      pageCount={pageCount}
                      marginPagesDisplayed={1}
                      pageRangeDisplayed={5}
                      onPageChange={this.handlePageClick}
                      containerClassName={"pagination"}
                      subContainerClassName={"pages pagination"}
                      activeClassName={"active"}
                      hrefBuilder={()=> <a href=""></a> } />:""
        }
      </StyledPodcastsPagination>
    );
  }
}

PodcastsPagination.propTypes = {
};

PodcastsPagination.contextTypes = {
  store : PropTypes.object
}



