import React from 'react';
import {StyledMoreUserInfos,StyledMoreUserInfosBody,InfosHeaderText,InfosList} from '../../styledComponents/styledComponents.jsx';


const MoreUserInfos = ({id,idv3,country,linkProfile,token}) =>
          <StyledMoreUserInfos>
            <InfosHeaderText>More...</InfosHeaderText>
            <StyledMoreUserInfosBody>
                <InfosList type={"more"}>
                  <li><span>Id account:</span> {id}</li>
                  <li><span>Idv3:</span> {idv3}</li>
                  <li><span>Country:</span> {country}</li>
                  <li><span>Link to profile:</span>
                    <a href={linkProfile}>{linkProfile}</a>
                  </li>
                  <li><span>Login token:</span> {token}</li>
                </InfosList>
            </StyledMoreUserInfosBody>
          </StyledMoreUserInfos>

export default MoreUserInfos;



