import React from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';
import {Redirect} from 'react-router-dom';
import PodcastsPagination from './podcastsPagination.jsx';
import AppTemplate from '../appTemplate.jsx';
import {HeaderText,HeaderButton,FlattrHeader,FlattrBody} from '../../styledComponents/styledComponents.jsx';
import {updateFlattredItems,initializeFlattredList} from '../../redux/actions.js';

export default class FlattredDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      redirectTo : null
    }

    this.onRefreshClick = this.onRefreshClick.bind(this);
    this.onDeleteAllClick = this.onDeleteAllClick.bind(this);
  }

  onRefreshClick(e){
    e.preventDefault();

    const {user} = this.context.store.getState();
    const {store} = this.context;

    $(document).trigger('loadingRefresh');
    fetch('https://flattrback.herokuapp.com/firebase/items/' + user.id ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                 if(data.items)
                   store.dispatch(updateFlattredItems(data.items));
                 else
                   store.dispatch(initializeFlattredList());

                 $(document).trigger('flattredRefreshed');
                 $(document).trigger('refreshLoaded');
             })
             .catch(err => {
                       $(document).trigger('refreshLoaded');
                       alert(JSON.stringify("error : " + err));
             });
  }

  onDeleteAllClick(e){
    e.preventDefault();
    const {user} = this.context.store.getState();
    const {store} = this.context;

    $(document).trigger('loadingDeleteAll');
    fetch('https://flattrback.herokuapp.com/firebase/deleteAll/' + user.id ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                 store.dispatch(initializeFlattredList());
                 //alert("success:" + JSON.stringify(data));

                 $(document).trigger('flattredReset');
                 $(document).trigger('deleteAllLoaded');
                 alert('all deleted successfully');
             })
             .catch(err => {
                   $(document).trigger('deleteAllLoaded');
                   alert("a server error has occured. Try again");
             });
  }

  componentWillUpdate(nextProps, nextState) {

  }

  componentWillMount() {
    const {token} = this.context.store.getState();
    //alert(token.value);
    if(!token.value)
      this.setState({redirectTo : '/signIn'});


  }

  componentDidMount() {
  }

  render() {
    const {redirectTo} = this.state;
    const {user} = this.context.store.getState();

    return (
      (redirectTo)?
      <Redirect to={redirectTo} />:
      <AppTemplate title="flattred">
        <FlattrHeader>
         <HeaderText>Podcasts marked for flattring...</HeaderText>
         <HeaderButton>
           <button type="button" onClick={this.onRefreshClick} className="flattredHeaderButton">
             refresh
           </button>
           <button type="button" onClick={this.onDeleteAllClick} className="flattredHeaderButton">
             delete all
           </button>
         </HeaderButton>
        </FlattrHeader>
        <FlattrBody>
          <PodcastsPagination type="flattred" />
        </FlattrBody>
      </AppTemplate>
    );
  }
}

FlattredDashboard.propTypes = {
};

FlattredDashboard.contextTypes = {
  store : PropTypes.object
}



