import React from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';
import PodcastItem from './podcastItem.jsx';
import MarkedPodcastItem from './markedPodcastItem.jsx';
import Loader from './loader.jsx';
import {StyledPodcastItems,NoItem} from '../../styledComponents/styledComponents.jsx';

export default class PodcastItems extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading : false
    }
  }

  componentDidMount() {
    $(document).on('loadingItems',(e)=>{
       this.setState({loading : true});
    });

    $(document).on('itemsLoaded',(e)=>{
      this.setState({loading : false});
    });

    $(document).on('loadingRefresh',(e)=>{
      this.setState({loading : true});
    });

    $(document).on('refreshLoaded',(e)=>{
      this.setState({loading : false});
    });

    $(document).on('loadingDeleteAll',(e)=>{
      this.setState({loading : true});
    });

    $(document).on('deleteAllLoaded',(e)=>{
      this.setState({loading : false});
    });
  }
  render() {
    const {pageData,className,type} = this.props;
    const {loading} = this.state;

    return (
        (loading)? <Loader loaded={false} /> :
        <StyledPodcastItems>
          {
            (type === "hot" && pageData.length)?
                    pageData.map((item,i) => <PodcastItem item={item} key={i} id={i} /> ) :
                (type === "flattred" && pageData.length)?
                    pageData.map((item,i) => <MarkedPodcastItem item={item} key={i} id={i} /> ) :
                    <NoItem>No Item Yet</NoItem>
          }
        </StyledPodcastItems>);
  }
}

PodcastItems.propTypes = {
};

PodcastItems.contextTypes = {
  store : PropTypes.object
}



