import React, {PropTypes} from 'react';
import {Buttons} from '../../styledComponents/styledComponents.jsx';

const PodcastButtons = ({podcastItemSubscribed,podcastItemActionUnmark,subscribed}) =>
      <Buttons>
        <button type="button" className="markedPodcastItemButton" onClick={(e)=> podcastItemSubscribed(e,subscribed)}>
          {(subscribed)?"disable" : "subscribe" }
        </button>
        <button type="button" className="markedPodcastItemButton" onClick={(e)=> podcastItemActionUnmark(e)}>unmark</button>
      </Buttons>


export default PodcastButtons;



