import React from 'react';
import PropTypes from 'prop-types';
import {Redirect} from 'react-router-dom';
import $ from 'jquery';
import AppTemplate from '../appTemplate.jsx';
import SearchItems from './searchItems.jsx';
import PodcastsPagination from './podcastsPagination.jsx';
import {PodHeader,PodBody,HeaderText} from '../../styledComponents/styledComponents.jsx';
import {updateHotResults} from '../../redux/actions.js';
import '../../common/css/pagination.css';


export default class PodcastsDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      redirectTo : null
    }

    this.onSearchAllClick = this.onSearchAllClick.bind(this);
  }

  componentWillMount() {
    const {token} = this.context.store.getState();
    //alert(token.value);
    if(!token.value)
      this.setState({redirectTo : '/signIn'});

  }

  onSearchAllClick(e){
    e.preventDefault();
    const {store} = this.context;
    $(document).trigger('loadingItems');
    const parametersList = "term=all&media=podcast&limit=200";
    fetch('https://itunes.apple.com/search?' + parametersList  ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                     //console.log(JSON.stringify(data));
                     var results = data.results;
                     store.dispatch(updateHotResults(data.results));
                     //console.log(res);
                     $(document).trigger('newhotResults');
                     console.log(JSON.stringify(data));
                     $(document).trigger('itemsLoaded');

             })
             .catch(err => {
                        $(document).trigger('itemsLoaded');
                        alert("an error occured. Try again");

             });
  }

  render() {
    const {redirectTo} = this.state;
    const {user,hotResults} = this.context.store.getState();

    return (
      (redirectTo)?
      <Redirect to={redirectTo} />:
      <AppTemplate>
            <PodHeader>
               <p><span>Hello</span> <br/>{user.username}</p>
               <SearchItems onSearchAllClick={this.onSearchAllClick} />
            </PodHeader>
            <PodBody>
                <HeaderText type="all">
                  Available podcasts shows : {hotResults.length}
                </HeaderText>
                <PodcastsPagination type="hot"/>
            </PodBody>
      </AppTemplate>);
  }
}

PodcastsDashboard.propTypes = {
};

PodcastsDashboard.contextTypes = {
  store : PropTypes.object
}



