import React, {PropTypes} from 'react';
import {Buttons} from '../../styledComponents/styledComponents.jsx';

const PodcastButtons = ({podcastItemAction,item}) =>
      <Buttons>
        <button type="button" className="podcastItemButton" onClick={(e)=> podcastItemAction(e,'mark')}>mark</button>
        <button type="button" className="podcastItemButton" onClick={(e)=> podcastItemAction(e,'remove')}>remove</button>
      </Buttons>


export default PodcastButtons;



