import React, {PropTypes} from 'react';
import ReactLoader from 'react-loader';
import {StyledLoader,LoaderImg} from '../../styledComponents/styledComponents.jsx';

export default class Loader extends React.Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    const {size} = this.props;
    var scale ;
    if(size === "small")
       scale = 0.45;
    else
       scale = 0.73;

    this.options = {
        lines: 13,
        length: 13,
        width: 10,
        radius: 30,
        scale: scale,
        corners: 1,
        color: '#000',
        opacity: 0.25,
        rotate: 0,
        direction: 1,
        speed: 2,
        trail: 60,
        fps: 20,
        zIndex: 2e9,
        top: '50%',
        left: '50%',
        shadow: false,
        hwaccel: false,
        position: 'absolute'
    }
  }
  render() {
    const {loaded,size,noLogo} = this.props;
    return (
      <StyledLoader>
         {
           (noLogo)? "":
              <LoaderImg src={require(`../../common/images/logo.png`)} alt="" size={size}/>
         }
         <ReactLoader laoded={loaded} options={this.options} className="spinner"/>
      </StyledLoader>
    );
  }
}

Loader.propTypes = {
};



