import React, {PropTypes} from 'react';
import SearchField from './searchField.jsx';
import {StyledSearchItems} from '../../styledComponents/styledComponents.jsx';

export default class SearchItems extends React.Component {
  render() {
    const {onSearchAllClick} = this.props;
    return (
      <StyledSearchItems>
          <SearchField />
          <p>OR</p>
          <button className="search-all" onClick={(e)=> onSearchAllClick(e)}>Search all podcasts</button>
      </StyledSearchItems>);
  }
}

SearchItems.propTypes = {
};



