import React from 'react';
import {StyledAppBarBackground} from '../styledComponents/styledComponents';
import backimage from '../common/images/appbarback.png';


const AppBarBackground = ()=>
        <StyledAppBarBackground>
          <img src={backimage} />
          <img src={backimage} />
        </StyledAppBarBackground>

export default AppBarBackground;



