import styled,{css} from 'styled-components';
import {injectGlobal} from 'styled-components';

injectGlobal`
  @import url('https://fonts.googleapis.com/css?family=Mako|BioRhyme+Expanded|Bevan|Crete+Round|Patua+One|Josefin+Sans|Changa+One|Macondo|Exo+2|Acme|Sigmar+One');
  `



export const flexRow = styled.div`
   display : flex;
   flex-direction : row;

`

export const flexColumn = styled.div`
   display : flex;
   flex-direction : column;

`
export const StyledAppTemplate = styled.div`
  position : absolute;
  width : 100%;
  top : 0 ;
  left : 0 ;
  background: #82e1e1;
  min-height : 100vh;
  overflow-x: hidden;

  /*#5db3e9*/
`

export const AppTemplateBody = styled.div`
  margin : 148px 0 0 0 ;
  /*border-top: 1px solid black;*/
`

export const StyledAppBar = styled.div`
  position : absolute;
  top : 0 ;
  left : 0 ;
  text-align: left;
  width : 100%;
  height : 149px;
  /*border-bottom: 1px solid black;*/
  background-image: url('../common/images/appbarback.png');
  z-index : 10;
  background-repeat: repeat-x;
`

export const BarLeftBox = styled.div`
  float : left;
  margin : 10px 0 0 100px;
  display: flex;
  flex-direction: row;

`

export const BarRightBox = styled.div`
  float: right;
  margin : 12px 40px 0 0;
  display: flex ;
  flex-direction: row;
  /*border : 1px solid black;*/
  justify-content: space-between;

`

export const StyledLink = styled.a`
  text-decoration: none;
  color : #444;
  &:hover {
    text-decoration: none;
  }
  &:active {
    color : #ddd;
  }
  &:focus {
    text-decoration: none
  }
`

export const StyledBarIcon = styled.img`
  width : 100px;
  height : 70px ;
`


export const StyledAppMenu = styled.ul`
  list-style: none ;
  margin : 15px 0 0 40px;
  padding : 0;
`

export const AppMenuItem = styled.li`
  display: inline;
  margin : 0 0 0 30px;
  padding : 0 0 17px 0 ;
  font-family: 'Patua One', cursive;

`

export const AppMenuLink = styled.a`
    color : #333;
    text-decoration: none;
    cursor: pointer;
    font-size: 25px;
    font-weight: 600;
    font-family: "Acme";
    padding : 5px 8px;
    border-radius: 8px;
    border : 2px solid black;

    transition: background-color .8s ease;
    &:hover {
      background-color: #9fe5fb;
      color : white;

    }

`

export const Introduction = styled.p`
  font-size: 25px;
  font-family: 'Patua One', cursive;
  margin : 0 0 0 30px;
`

