export const MoreButton = styled.button`

  margin : 5px 0 0 0;
  width :70px;
  height : 30px;
  font-size: 15px;
  background: none;
  border: none;
  font-weight: 800;
  font-family: 'Josefin Sans', sans-serif;
  cursor: pointer;
  color : #e70966;
  text-decoration: underline;

  transition: background .5s ease-in-out;
  transition: color .5s ease-in-out;

  &:hover {
     background: #e70966;
     color : white;
     text-decoration : none;
  }

  &:focus {
    outline: none;
  }

`

