import React from 'react';
import PropTypes from 'prop-types';
import Loader from './loader.jsx';
import $ from 'jquery';
import {StyledPodcastItemMore,CloseModalButton,ArtworkImg,HeaderInfos,BodyContent,
  EpisodeAudio,EpisodePubdate,EpisodeHeaderText,EpisodeTitle,EpisodeDescription,EpisodesButton} from '../../styledComponents/styledComponents.jsx';
import {updateLastEpisode} from '../../redux/actions.js';


export default class MarkedPodcastItemMore extends React.Component {
  constructor(props) {
    super(props);
    this.state ={
      loading : false
    }
    this.onLastMonthClick = this.onLastMonthClick.bind(this);
    this.onLastWeekClick = this.onLastWeekClick.bind(this);
    this.submitFlattr = this.submitFlattr.bind(this);
  }

  onLastMonthClick(e){
    e.preventDefault();
  }

  onLastWeekClick(e){
    e.preventDefault();
  }

  submitFlattr(e){
    const {item} = this.props;
    const {token} = this.context.store.getState();
    e.preventDefault();

    const payload = {token : token.value, url : item.collectionViewUrl.toString(),
                     title : item.collectionName}

    this.setState({loading : true});
    fetch('https://flattrback.herokuapp.com/flattr' ,{method : "post",
          body : JSON.stringify(payload),
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                  if(data.success && data.data.error)
                    alert(data.data.error_description);
                  else{
                    alert(JSON.stringify(data));
                  }

                  this.setState({loading : false});

             })
             .catch(err => {
                  this.setState({loading : false});
                  alert("an error has occured. try again");
             });
  }

  render() {
    const {onClick,item} = this.props;
    const {loading} = this.state;

    return (
        <StyledPodcastItemMore>
          <CloseModalButton onClick={(e)=> onClick(e)} href="#">
            <img src={require(`../../common/images/remove_red.png`)} />
          </CloseModalButton>
          <div className="artWork">
            <ArtworkImg type="more" src={item.artworkUrl} alt="" />
          </div>
          <HeaderInfos>
             <p className="headerInfosTitle">{item.collectionName}</p>
             <p className="headerInfosSubtitle">By {item.artistName}</p>
             <a href={item.collectionViewUrl} title={item.collectionViewUrl}>
               {item.collectionViewUrl.substring(0,35)}...
             </a>
             {
               (loading)? <div style={{position:'absolute',top:10,right:30}}><Loader loaded={false} noLogo={true} size="small" /> </div>:
                     <button className="flattrButton" onClick={this.submitFlattr}>
                       submit for flattr
                     </button>
             }
          </HeaderInfos>
          <BodyContent>
              <div className="bodyContentLeft">
                  <EpisodeHeaderText>Search earlier episodes</EpisodeHeaderText>
                  <EpisodesButton onClick={this.onLastMonthClick}>Last month (TODO)</EpisodesButton>
                  <EpisodesButton onClick={this.onLastWeekClick}>Last week (TODO)</EpisodesButton>
              </div>
              <div className="bodyContentRight">
                 <EpisodeHeaderText>Listen to the last episode now</EpisodeHeaderText>
                 {
                   (item.lastEpisode && item.lastEpisode !== {})?
                     <div>
                       <EpisodeAudio controls>
                         <source src={item.lastEpisode.enclosures[0].url}
                            type={item.lastEpisode.enclosures[0].type}/>
                       </EpisodeAudio>
                       <EpisodePubdate>published on {item.lastEpisode.pubdate.split('.')[0]}</EpisodePubdate>
                       <EpisodeTitle>{item.lastEpisode.title}</EpisodeTitle>
                       <EpisodeDescription>
                         <span>ABOUT</span><br/>
                         {item.lastEpisode.description}
                       </EpisodeDescription>
                     </div> : ""
                 }

              </div>
          </BodyContent>
        </StyledPodcastItemMore>
    );
  }
}

MarkedPodcastItemMore.propTypes = {
};

MarkedPodcastItemMore.contextTypes = {
  store : PropTypes.object
}



