import React from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';
import {StyledSearchField} from '../../styledComponents/styledComponents.jsx';
import {updateHotResults} from '../../redux/actions.js';

export default class SearchField extends React.Component {
  constructor(props) {
    super(props);
    this.onClick = this.onClick.bind(this);
  }

  onClick(e){
    e.preventDefault();
    const {store} = this.context;

    const term = $('.podcast-search-input').val();

    $(document).trigger('loadingItems');
    const parametersList = `term=${term}&media=podcast&limit=200`;
    fetch('https://itunes.apple.com/search?' + parametersList  ,{method : "get",
          headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
          }})
             .then(res =>{  console.log(res); return  res.json(); })
             .then(data => {
                     //var res = results.map(result => result.country)
                     //console.log(res);
                     if(data.results.length){
                       store.dispatch(updateHotResults(data.results));
                       //console.log(res);
                       $(document).trigger('newhotResults');
                       console.log(JSON.stringify(data));
                     }
                     else {
                       //notification
                       alert('not found');
                     }

                     $(document).trigger('itemsLoaded');

             })
             .catch(err => {
                        $(document).trigger('itemsLoaded');
                        alert("an error occured. Try again");
             });
  }
  render() {
    return (
      <StyledSearchField>
       <input type="text" className="podcast-search-input"
         placeholder="give in a subject or show name" />
       <button className="search-submit" type="submit" onClick={this.onClick}>start off</button>
      </StyledSearchField>);
  }
}

SearchField.propTypes ={
};

SearchField.contextTypes = {
  store : PropTypes.object
}



