import React from 'react';
import PropTypes from 'prop-types';
import {Redirect} from 'react-router-dom';
import AppTemplate from '../appTemplate.jsx';
import HeaderUserInfos from './headerUserInfos.jsx';
import MoreUserInfos from './moreUserInfos.jsx';


export default class AccountDashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state ={
      redirectTo : null
    }

  }
  componentWillMount() {
    const {token} = this.context.store.getState();
    //alert(token.value);
    if(!token.value)
      this.setState({redirectTo : '/signIn'});

  }
  render() {
    const {redirectTo} = this.state;
    const {user,token} = this.context.store.getState();
    return (
      (redirectTo)?
      <Redirect to={redirectTo} /> :
      <AppTemplate title="account">
            <HeaderUserInfos
              avatar={user.avatar}
              username={user.username}
              email={user.email}
              firstname={user.firstname}
              lastname={user.lastname}
              />
            <MoreUserInfos
                id={user.id}
                idv3={user.idv3}
                country={user.country}
                linkProfile={user.link}
                token={token.value}
             />
      </AppTemplate>
    );
  }
}

AccountDashboard.contextTypes = {
  store : PropTypes.object
}

AccountDashboard.propTypes = {
};



