import React from 'react';
import Avatar from 'react-avatar';
import {StyledHeaderUserInfos,StyledHeaderUserInfosBody,InfosHeaderText,InfosList} from '../../styledComponents/styledComponents.jsx';


const HeaderUserInfos = ({avatar,username,email,firstname,lastname}) =>
          <StyledHeaderUserInfos>
            <InfosHeaderText>Your flattr account</InfosHeaderText>
            <StyledHeaderUserInfosBody>
                <Avatar value="86%" name="cloud strife" src={avatar} round={true} size={150}
                  style={{width : 150 , height : 150}} color="#000" />
                <InfosList type={"header"}>
                  <li><span>Username:</span> {username}</li>
                  <li><span>Email:</span> {email}</li>
                  <li><span>Full name:</span> {lastname} {firstname}</li>
                </InfosList>
            </StyledHeaderUserInfosBody>
          </StyledHeaderUserInfos>

export default HeaderUserInfos;



