import { createStore, combineReducers, applyMiddleware } from 'redux';
import {hotResults,flattredItems,token,user} from './reducers';


const initialState = {
  hotResults : [
    {
      wrapperType : "",
      artistId : "",
      collectionId : "",
      trackId : "",
      artistName : "",
      collectionName : "",
      trackName : "",
      artworkUrl : "",
      artistViewUrl : "",
      collectionViewUrl : "",
      feedUrl : "",
      releaseDate : "",
      country : "",
      genres : []

    }
  ],
  flattredItems : [],
  token : {
    value : "",
    expireAt : null
  },
  user : {
    id : "",
    idv3 : "",
    username : "",
    email : "",
    firstname : "",
    lastname : "",
    country : "",
    link : "",
    avatar : ""
  }
}

const logger = store => next => action => {
    let result;
    console.groupCollapsed("dispatching", action.type);
    console.log('prev state', store.getState());
    console.log('action', action);
    result = next(action);
    console.log('next state', store.getState());
    console.groupEnd();
}


const storeFactory = () => {
        return applyMiddleware(logger)(createStore)(
            combineReducers({hotResults,flattredItems,token,user}),
            (localStorage['redux-store'])?JSON.parse(localStorage['redux-store']):initialState
        )
}

export default storeFactory ;



