import React from 'react';
import $ from 'jquery';
import {StyledAppMenu,AppMenuItem,AppMenuLink} from '../styledComponents/styledComponents.jsx';


class AppMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }

  }

  render(){
    const {title} = this.props;
    return (
    <StyledAppMenu>
      <AppMenuItem>
        <AppMenuLink  href="#/dashboard/pods" >
             podcasts
        </AppMenuLink>
      </AppMenuItem>
      <AppMenuItem>
        <AppMenuLink  href="#/dashboard/flattred" >
             flattred
        </AppMenuLink>
      </AppMenuItem>
      <AppMenuItem>
        <AppMenuLink  href="#/dashboard/account" >
            account
        </AppMenuLink>
      </AppMenuItem>
    </StyledAppMenu>
  )
  }
}

export default AppMenu ;



